  /* .dapps_content li:hover{
      box-shadow:0px 0.45rem 40px 0px rgba(34,34,34,0.1);
      top: -0.22rem;
      transition:0.2s all linear;
  } */
  .dapps_img {
    display: flex;
    justify-content: center;
    margin-bottom: 0.29rem;
  }

  .dapps_img img {
    vertical-align: top;
    width: 0.72rem;
    height: 0.72rem;
  }

  .dapps_tit {
    text-align: center;
    font-weight: Bold;
    color: #333333;
    font-size: 0.26rem;
    line-height: 0.28rem;
    margin-bottom: 0.34rem;
  }

  .dapps_text {
    color: #999999;
    line-height: 0.36rem;
    font-size: 0.24rem;
    /* white-space: initial;
    white-space:normal;
    word-break:break-all; */
    /* word-break:break-all; */
    /* white-space:pre-wrap; */
    word-wrap: break-word;
    white-space: normal;
    word-break: break-all;
  }

  .dapps_state_box {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0.24rem 0 0.3rem;
  }

  .dapps_state_box a {
    color: #333333;
    font-size: 0.24rem;
    padding-right: 0.16rem;
    position: relative;
    display: flex;
    align-items: center;
    color: #190E5D;
  }

  .dapps_state_box a::after {
    content: "";
    position: absolute;
    width: 0.06rem;
    height: 0.12rem;
    background: url("../../assets/img/lanjiantou.png") no-repeat;
    background-size: 100%;
    right: 0px;
  }

  .course {
    padding-top: 1.5rem;
  }

  .course_content {
    margin-top: 0.8rem;
    overflow: hidden;
    overflow-x: auto;
  }

  .course_box {
    width: 14.04rem;
  }

  .course_img {
    width: 14.04rem;
    height: 2.75rem;
    /* vertical-align: top; */
  }

  .course_wrap_top, .course_wrap_bottom {
    display: flex;
    justify-content: space-around;
  }

  .course_wrap_top {
    margin-bottom: 0.3rem;
  }

  .course_wrap_bottom {
    margin-top: -1.18rem;
  }

  .course_wrap_top div, .course_wrap_bottom div {
    width: 1.58rem;
  }

  .course_time {
    font-size: 0.18rem;
    line-height: 0.24rem;
    font-weight: bold;
    color: #333333;
  }

  .course_text {
    font-size: 0.16rem;
    color: #999999;
    line-height: 0.3rem;
    margin-top: 0.23rem;
  }

  .news_box {
    width: 100%;
    margin-top: 1.50rem;
  }

  .news {
    display: flex;
    flex-direction: column;
    margin-top: 0.52rem;
  }

  .news li {
    display: flex;
    justify-content: space-around;
    padding: 0.3rem 0.2rem 0.4rem;
    box-sizing: border-box;
    border-bottom: 1px solid #F5F5F5;
  }

  .news li:nth-last-child(1) {
    border-bottom: 0;
  }

  .news li .news_img {
    width: 3.40rem;
    height: 2.06rem;
    border-radius: 0.1rem;
    overflow: hidden;
    display: inline-block;
  }

  .news li .news_img img {
    width: 100%;
    height: 100%;
  }

  .news li .news_text {
    width: 3.13rem;
    /* margin-left: 0.28rem; */
    display: inline-block;
  }

  .news li .news_text p:nth-child(1) {
    font-size: 0.26rem;
    font-weight: Bold;
    color: #333333;
    line-height: 0.36rem;
  }

  .news li .news_text p:nth-child(2) {
    font-size: 0.24rem;
    line-height: 0.36rem;
    margin-top: 0.1rem;
    margin-bottom: 0.3rem;
    color: #999999;
    text-overflow: -o-ellipsis-lastline;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .news li .news_text a:nth-child(3) {
    /* display: block; */
    width: 1rem;
    font-size: 0.22rem;
    line-height: 0.24rem;
    color: #190E5D;
    padding-right: 0.16rem;
    position: relative;
    display: flex;
    align-items: center;
  }

  .news li .news_text a:nth-child(3)::after {
    content: "";
    width: 0.08rem;
    height: 0.16rem;
    position: absolute;
    background: url("../../assets/img/lanjiantou.png") no-repeat;
    background-size: 100% 100%;
    right: 0;
  }

  .footer {
    width: 7.50rem;
    height: 8.13rem;
    background: url("../../assets/mobileImg/dibubj.png") no-repeat;
    background-size: 100% 100%;
    padding: 0.79rem 0.3rem 0.8rem;
    box-sizing: border-box;
  }

  .footer_tit p:nth-child(1) {
    font-size: 0.3rem;
    color: #FFFFFF;
    font-weight: 500;
    margin-bottom: 0.29rem;
  }

  .footer_tit p:nth-child(2) {
    font-size: 0.26rem;
    color: #FFFFFF;
    margin-bottom: 0.8rem;
    /* font-weight: lighter; */
  }

  .footer_content {
    display: flex;
    justify-content: space-between;
  }

  .footer_content ul {
    width: 2.8rem;
  }

  .footer_content ul li {
    margin-top: 0.59rem;
  }

  .footer_content ul li:nth-child(1) {
    margin-top: 0;
  }

  .footer_content ul li p:nth-child(1) {
    font-size: 0.24rem;
    color: #FFFFFF;
    font-weight: 500;
    margin-bottom: 0.24rem;
  }

  .footer_content ul li p:nth-child(2) {
    font-size: 0.22rem;
    color: #FFFFFF;
  }

  .code {
    width: 3.4rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  .code img {
    width: 2.3rem;
    height: 2.3rem;
  }

  .code p:nth-child(2), .code p:nth-child(3) {
    font-size: 0.22rem;
    color: #FFFFFF;
  }

  .code p:nth-child(2) {
    margin: 0.38rem 0 0.19rem;
  }

  .address {
    font-size: 0.22rem;
    color: #FFFFFF;
    margin-top: 0.87rem;
  }

  .address p {
    padding-left: 0.3rem;
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 0.22rem;
  }

  .address p:nth-child(2) {
    margin-bottom: 0;
  }

  .address p::after {
    content: "";
    position: absolute;
    left: 0;
    width: 0.2rem;
    height: 0.2rem;
    background: url("../../assets/mobileImg/dizhi.png") no-repeat;
    background-size: 100% 100%;
  }
</style>



